$color-primary : #1e79f3;
$color-seconds : #e2a64e;
$color-third : #FF6F91;
$color-fourth : #FF9671;
$color-fifth : #FFC75F;
$color-sixth : #F9F871;


.ModalPanel {
    animation-duration: 0.2s;
    animation-name: slidein;
  }
  
  @keyframes slidein {
    from {
      margin-top: 100%;
    }
  
    to {
        margin-bottom: 0%;
    }
  }

.ModalBackground{
    width:100vw;
    height:100vh;
    background-color:#00000055;
    display:flex;
    justify-content:center;
    align-items:center;
    z-index: 1000;
    position:fixed;
    top:0;
    left: 0;

    .ModalPanel{
        width: 20rem;
        border-radius: 20px;
        background-color: whitesmoke;
        border : 3px solid $color-primary;
        max-width: 90%;
        
        .ModalHeader{
            width: 100%;
            height: 10%;
            min-height: 2rem;
            display: flex;
            align-items: center;
            border-bottom : 2px solid $color-primary;
            position: relative;
            .ModalCloseButton{
                position:absolute;
                top: 1vh;
                right: 2vh;
            }
            .ModalCloseButton > *{
                cursor: pointer;
                color: $color-primary;
            }
            .ModalTitle{
                font-weight: bold;
                padding-left: 1rem;
                font-size: 100%;
                color: $color-primary;
            }
        }

        .ModalContents{
            padding: 2%;
            margin: 1px 0;
            .ModalContentsBody{
                width: 100%;
                height: 100%;
                // border : 2px solid $color-primary;
                overflow: auto;
            }
        }
        .ModalFooter{
            width: 100%;
            padding: 0 1rem;
            display: flex;
            bottom: 1%;
            align-items:center;
            justify-content:flex-end;
        }
        .ModalFooter > *{
            margin: 1.5% 1%;
            max-height: 90%;
        }
        .ModalFooter> *:last-child{
            margin-right: 0;
        }
    }
    
}
