
.Loading{
    width: 100%;
    height: 100%;
    position: fixed;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #0000002d;
    z-index: 1000000;

    .LoadingDiv{
        width:60%;
        height:10%;
        max-width: 20rem;
        max-height: 10rem;
        background-color: rgba(85, 173, 255, 0.63);
        display:flex;
        align-items:center;
        color: white;
        justify-content: center;
        border-radius: 20px;
        text-align: center;
        font-size: 4vw;
    }
}