
.Timer_box{
    width: 100%;
    height: 14%;
    background-color: $color-third;
    display: flex;
    border-top: 2px solid;
    border-bottom: 2px solid;
    animation: fade-in 2s;
    
    .datetime{
        width: 50%;
        height: 100%;
        justify-content: center;
        align-items: center;
        font-weight: 700;
        display: flex;
        .date{
            width: 50%;
            display: flex;
            justify-content: center;
            font-size: 3vmin;
            word-break: keep-all;
            text-align: center;
        }
        .time{
            width: 50%;
            display: flex;
            justify-content: flex-start;
            font-size: 4vmin;
        }
    }

    .Timer_control_box{
        width: 50%;
        height: 100%;
        display: flex;
        justify-content: center;
        gap: 1vw;
        align-items: center;

        .timer_btn {
            width: 80px !important;
            min-width: 0;
            height: 80px;
            font-size: 6vmin;
        }

    }
}