@import '../anim/anim.scss';

.alertPanel {
    animation-name: slideinBottomAlert;
    animation-duration: 0.5s;
    animation-fill-mode: forwards;
  }

.alertBackground{
    width:100vw;
    height:100vh;
    position:fixed;
    top:0;
    left:0;
    background-color:#00000055;
    display:flex;
    justify-content:center;
    align-items:center;
    z-index: 100001;

    .alertPanel{
        background-color: white;
        width:25rem;
        max-width: 90%;
        height: auto;
        border-radius: 20px;
        margin-bottom: -1500px;
        .alertBody{
            width: 100%;
            display: flex;
            min-height: 15vh;
            flex-direction: column;
            gap: 1rem;
            justify-content: center;
            align-items: center;
            padding: 1rem;
            overflow-y: hidden;
            overflow-x: hidden;
            
            .alertText {
                font-size: 1.5rem;
                word-break: keep-all;
                text-align: center;
                line-height: 110%;
                .strong{
                    color:#1890ff
                }
            }
            .alertInput{
                width: 80%;
                border-top: none;
                border-left: none;
                border-right: none;
            }
            .alertInput:focus{
                outline: none;
                border-top: none;
                border-left: none;
                border-right: none;
                border-color: #1890ff;
            }
        }

        .alertFooter{
            width: 100%;
            height: 3rem;
            margin-bottom: 0.5rem;
            display: flex;
            justify-content: space-around;
            align-items: center;

            .okBtn{
                width: 30%;
                height: 80%;
                background-color: #001529;
                border-radius: 20px;
                display: flex;
                justify-content: center;
                align-items: center;
                color: white;
                font-size: 1.5rem;
                cursor: pointer;
            }
            .okBtn:hover{
                background-color: #1890ff;
            }
            .cancelBtn{
                width: 30%;
                height: 80%;
                background-color: #ff3e3e;
                border-radius: 20px;
                display: flex;
                justify-content: center;
                align-items: center;
                color: white;
                font-size: 1.5rem;
                cursor: pointer;
            }
            .cancelBtn:hover{
                background-color:#fa0000;
            }
        }
    }
}
