
  @keyframes slideinBottom {
    from {
        background-position: center 100px;
    }
  
    to {
        background-position: center;
    }
  }
  @keyframes fadein {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
    }
@-webkit-keyframes fadein { /* Safari and Chrome */
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
        }
    }

    @keyframes slideinBottomAlert{
        from {
           margin-bottom: -500px;
        }
      
        to {
            margin-bottom: 0px;
        }
      }