.sleep_panel{
    position: fixed;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.8);
    width: 100%;
    height: 100%;
    display: none;
    justify-content: center;
    align-items: center;
    color: whitesmoke;


    &.appear {
        animation: fade-in 1s;
        animation-fill-mode: forwards;
        display: flex;
    }
      
    &.disappear {
        animation: fade-out 1s;
        animation-fill-mode: forwards;
    }
    .comment_box{
        display: grid;
        justify-items: center;
        gap: 5vw;
    }


    .sentence{
        font-size: 8vw;
        width: 80%;
        word-break: keep-all;
        line-height: 5vh;
    }
    .check_btn{
        font-size: 5vw;
        position: absolute;
        bottom: 10vw;
        right: 10vw;
        border-bottom: 3px solid white;
        padding-bottom: 1vw;
    }
}

//쓰러졌을때 경고창
.faint_alert{
    position: fixed;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.8);
    width: 100%;
    height: 100%;
    display: none;
    justify-content: center;
    align-items: center;
    color: whitesmoke;


    &.appear {
        animation: fade-in 1s;
        animation-fill-mode: forwards;
        display: flex;
    }
      
    &.disappear {
        animation: fade-out 1s;
        animation-fill-mode: forwards;
    }


    .sentence{
        font-size: 7vw;
        width: 80%;
        word-break: keep-all;
        line-height: 5vh;
    }
    .check_btn{
        font-size: 5vw;
        position: absolute;
        bottom: 10vw;
        right: 10vw;
        border-bottom: 3px solid white;
        padding-bottom: 1vw;
    }
    .tip{
        font-size: 3vw;
    }
}