//나의 자산 모달
.asset_body{
    width:100%;
    height:100%;
    display:grid;
    grid-template-rows: 90% 10%;


    @mixin listContainer {
        height: 40%;
        padding: 1vw;
        border: 3px outset $color-seconds;
        border-radius: 20px;
        display: grid;
        grid-auto-rows: 20%;
        gap: 1vh;
        overflow: auto;
        max-width: 80vw;
        .header{
            span{
                font-weight: 700 !important;
                font-size: 3.5vw !important;
            }
        }
    }
    @mixin listContents {
        display: flex;
            span{
                word-break: keep-all;
                text-align: center;
                font-size: 4vw;
                flex: 0 0 25%;
                display: flex;
                justify-content: center;
                align-items: center;

                &.plus{
                    color: red;
                }
                &.minus{
                    color: blue;
                }
            }
    }


    //나의 자산 탭
    .my_asset{
        height: 100%;
        padding:3vw;
        overflow-y: auto;
        @mixin contents {
            width: 100%;
            font-weight: 700;
            font-size: 3vh;
            display: flex;
            margin-bottom: 3vw;
        }
        & > .title{
            @include contents();
            padding-left: 5%;
            justify-content: flex-start;
            margin-top: 3vw;
            gap: 2vw;
            
            .money{
                display: flex;
                align-items: flex-end;
                font-size: 2vh;
                font-style: italic;
                .krw_icon{
                    width: 4vw;
                    height: 4vw;
                }
            }
        }
        .value{
            @include contents();
            justify-content: flex-end;
            padding-right: 10%;
            font-style: italic;
        }
        .list{
            @include listContainer();
            .list_contents{
                @include listContents();
                [class^=button]{
                    height: 80%;
                }
            }
        }
    }

    //은행 탭
    .bank{
        height: 100%;
        padding: 3vw;
        overflow-y: auto;
        .title{
            margin-top: 0.5vh;
            margin-bottom: 0.5vh;
            span{
                font-size: 2.5vh;
            }
        }
        .list{
            @include listContainer();
            .list_contents{
                @include listContents();
            }
        }
    }
    
    //코인장
    .coin{
        height: 100%;
        .table{
            height: 100%;
            .header{
                display: grid;
                grid-template-columns: 1fr 1fr 1fr;
                justify-items: center;
                align-items: center;
                height: 8%;
            }
            .body{
                display: grid;
                overflow-y: auto;
                height: 90%;
                grid-auto-rows: 10%;
                .row{
                    display: grid;
                    grid-template-columns: 1fr 1fr 1fr;
                    justify-items: center;
                    align-items: center;
                }
            }

        }
        

    }


    .tab_container{
        display:grid;
        grid-template-columns:repeat(3, 1fr);
        .tab_button{
            border-top:3px solid $color-primary;
            &.selected{
                background-color: lighten($color-primary, 10);
                color: whitesmoke;
                &:first-child{
                    border-bottom-left-radius: 20px;
                }
                &:last-child{
                    border-bottom-right-radius: 20px;
                }
            }
            &:nth-child(2){
                border-left:3px solid $color-primary;
                border-right:3px solid $color-primary;
            }
        }

    }
}


