//상점
.shop_modal{
    height: 100%;
    overflow-y: auto;

    .search_bar{
        display: grid;
        grid-template-columns: 1fr 1fr 1fr 1fr;
        grid-template-rows: 3vh;
        justify-items: center;
        align-items: center;
        margin-bottom: 2vw;
        font-size: 3.5vw;
        input{
            width: 100%;
            height: 100%;
        }
        select{
            width: 100%;
            height: 100%;
        }
    }
    .shop_list{
        height: 95%;
        width: 100%;

        .product_box{
            width: 100%;
            padding: 2vw;
            margin-bottom: 2vw;
            display: grid;
            grid-template-columns: 2fr 3fr 1fr;
            border: 2px outset $color-primary;
            border-radius: 10px;
            gap: 1vw;
            font-size: 3.4vw;
            .product_image{
                grid-row: span 5;
                width: 100%;
                height: 100%;
            }
            .buy_btn{
                grid-row: span 4;
                [class^=button]{
                    width: 100%;
                    height: 70%;
                }
            }
            .product_category{
                text-transform: uppercase;
            }
            .product_name{
                white-space: nowrap;
                text-overflow: ellipsis;
                overflow: hidden;
                color: $color-third;
                margin-bottom: 3vw;
                font-size: 4vw;
            }
            .product_price{
                color: $color-fourth;
            }
            .tag_list{
                grid-column: span 3;
                display: grid;
                grid-template-columns: 1fr 1fr 1fr 1fr;
                .tag{
                    @include subTag();
                }
            }
        }
    }
}

