    }
}



.equip_modal{
    .ModalContents{
        height: 60vh;
        .product_box{
            width: 100%;
            padding: 2vw;
            margin-bottom: 2vw;
            display: grid;
            grid-template-columns: 1fr 2fr;
            border: 2px outset $color-primary;
            border-radius: 10px;
            gap: 1vw;
            .product_image{
                grid-row: span 6;
                width: 100%;
                height: 100%;
            }
            .product_category{
                text-transform: uppercase;
            }
            .product_name{
                white-space: nowrap;
                text-overflow: ellipsis;
                overflow: hidden;
            }
            .tag_list{
                grid-column: span 2;
                display: grid;
                grid-template-columns: 1fr 1fr 1fr 1fr;
                .tag{
                    @include subTag();
                }
            }
        }

        .subItem{
            border: 2px solid $color-third;
            border-radius: 10px;
            margin-bottom: 2vw;
            padding: 2vw;
            .title{
                font-size: 5vw;
                text-transform: uppercase;
            }
        }
    }
}


.tag_modal{
    .ModalContents{
        height: 50vh;
        .search_bar{
            display: grid;
            grid-template-columns: 1fr 1fr 2fr;
            grid-template-rows: 6vw;
            justify-items: center;
            align-items: center;
            margin-top: 1vw;
            margin-bottom: 1vw;
            height: 4vh;
            input{
                margin: 0;
                width: 100%;
                height: 100%;
            }
        }

        .tag_container{
            display: grid;
            grid-template-columns: 1fr 1fr;
            align-items: center;
            justify-items: center;
            margin-bottom: 2vw;
            padding: 4vw;
            border: 1px solid $color-primary;
            border-radius: 10px;
            
        }
    }
}

.setting_modal{
    .ModalContents{
        overflow-y: auto;

        .options{
            width: 100%;
            height: 20%;
            margin-top: 20px;
            .header{
                width: 100%;
                display: flex;
                justify-content: space-between;
                align-items: center;
                .title{
                    font-size: 6vw;
                }
            }
            .range{
                width: 100%;
                height: 10%;
                margin-top: 5px;
                display: flex;
                align-items: center;
                input{
                    width: 90%;
                }
            }
            .logout_btn{

                width: 100%;
                height: 50px;
            }
        }
    }
}

.notice_modal{
    .ModalContents{
        height: 60vh;
        .ModalContentsBody{
            .notive_contents_wrapper{
                width: 100%;
                height: 6vh;
                margin-top: 2vh;
                border: 3px outset $color-primary;
                border-radius: 10px;
                display: flex;
                align-items: center;
                padding: 0 5%;
                text-overflow: ellipsis;
                overflow:hidden;
                white-space: nowrap;
                &.header{
                    border-color: red;
                    background-color: tomato;
                    color: white;
                }
            }
        }
    }
}

.notice_detail_modal{
    .ModalContents{
        height: 40vh;
        .ModalContentsBody{
            padding: 0 2%;
            .title{
                height: 5vh;
                font-size: medium;
                display: flex;
                align-items: center;
                justify-content: center;
                text-align: center;
                word-break: keep-all;
            }
            .date{
                width: 100%;
                display: flex;
                justify-content: flex-end;
            }
            .contents{
                width: 100%;
                word-break: break-all;
                white-space: pre-line;
                line-height: 150%;
            }
        }
    }
}

.inventory_modal{
    .ModalContents{
        height: 70vh;
        .ModalContentsBody{
            .panel{
                .contents{
                    font-size: small;
                    margin-bottom: 1rem;
                    word-break: keep-all;
                }
            }
        }
    }
}



.inventory_item_modal{
    .ModalContents{
        height: 20vh;
        .ModalContentsBody{
            .dashboard{
                height: 75%;
                .my_status{
                    display: flex;
                    justify-content: center;
                }
                .slot_number{
                    display: flex;
                    justify-content: center;
                    font-size: 3rem;
                    margin: 1vh 0;
                }
                .my_coupon{
                    display: flex;
                    justify-content: center;
                }
                .info {
                    display: flex;
                    justify-content: center;
                    &.red{
                        color : tomato
                    }
                    &.blink{
                        animation: textBlink 1s linear infinite;
                    }
                }
            }
            .slot_button{
                height: 20%;
                [class^=button]{
                    height: 100%;
                }
            }
            
        }
    }
}

.advertisement_modal{
    .ModalContents{
        height: 55vh;
        padding: 0 !important;
        .ModalContentsBody{
            .advertisement_modal_list{
                height: 90%;
                overflow-y: auto;
                .quest_side{
                    display: flex;
                    flex-direction: column;
                    gap: 2vh;
                    padding: 1vh;
                    .quest_contents{
                        display: flex;
                        align-items: center;
                        justify-content: space-between;
                        padding: 0 1vmin;
                        width: 100%;
                        height: 5vh;
                        border: 2px solid;
                        border-radius: 10px;
                        &.accept{
                            border-color: $color-primary;
                        }
                        
                        .contents_name{
                            width: 80%;
                            text-overflow: ellipsis;
                            overflow: hidden;
                            white-space: nowrap;
                        }
                    }
                    .quest_detail_modal{
                        .ModalContents{
                            height: 55vh;
                            .detail_script{
                                padding-top: 5%;
                                height: 85%;
                                overflow-y: auto;
                                div{
                                    display: flex;
                                    align-items: center;
                                }
                            }
                            .detail_button{
                                height: 10%;
                                display: flex;
                                justify-content: flex-end;
                                padding-right: 2vmin;
                            }
                        }
                    }
                }

                .client_side{
                    width: 100%;
                    .client{
                        display: flex;
                        flex-direction: column;
                        width: 100%;
                        height: 100%;
                        gap: 8px;

                        .client_contents{
                            
                            .product_list{
                                margin-top: 1vmin;
                                margin-bottom: 1vmin;
                                border: 2px outset $color-seconds;
                                border-radius: 10px;
                                padding: 1vmin;
                                display: flex;
                                flex-direction: column;
                                gap: 1vmin;
                            }
                            &:last-child{
                                margin-bottom: 1vh;
                            }
                        }
                    }
                }
            }

            .tab_container{
                height: 10%;
                display:grid;
                grid-template-columns:repeat(2, 1fr);
                .tab_button{
                    border-top:3px solid $color-primary;
                    &:first-child{
                        border-right: 3px solid $color-primary;
                    }
                    &.selected{
                        background-color: lighten($color-primary, 10);
                        color: whitesmoke;
                        &:first-child{
                            border-bottom-left-radius: 15px;
                        }
                        &:last-child{
                            border-bottom-right-radius: 15px;
                        }
                    }
                }
        
            }
        }
    }
}