.stress_bar{
    width: 100%;
    height: 5%;

    display: grid;
    color: #FBEAFF;
    background-color: lighten($color-primary, 10);
    grid-template-columns: 15% 65% 18%;

    align-items: center;
    animation: fade-in 2s;
    div{
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 3vmin;
    }

    .bar{
        background-color: lightgray;
        width: 100%;
        height: 4vmin;
        max-height: 80%;
        justify-content:flex-start;
        border-radius: 3px;
        .current{
            height:100%;
            border-radius: 3px;
            transition: 0.5s;
        }
    }
    .stress_number{
        font-size: 3vmin;
    }
}