$color-primary : #1e79f3;
$color-seconds : #00AFFF;
$color-third : #e2a64e;
$color-fourth : #9E7700;
$color-fifth : #FFEECA;
$color-sixth : #C2A875;
$color-border : #0048B7;

// 버튼 기본 타입
@mixin button {
    min-width: 4rem;
    display: inline-block;
    border-radius: 5px;
    padding: 0.2%;
    margin : 0.1rem;
    font-size: 1em;
    border : 2px outset $color-border;
    border-top: none;
    border-left: none;
    padding: 2px;
    cursor:pointer;

    display: flex;
    justify-content: center;
    align-items: center;
    word-break: keep-all;
    text-align: center;

    // div{
    //   width: 100%;
    //   height: 100%;
    //   display: flex;
    //   justify-content: center;
    //   align-items: center;
    //   word-break: keep-all;
    //   text-align: center;
    // }
    
    input{
      color: black
    }
    img{
      width: 100%;
      height: 100%;
    }
    &:active{
      border-style: inset;
      border-right: none;
      border-bottom: none;
    }
  }


.button{
  
  &-standard{
    @include button;
    background-color:$color-primary;
    border-color : $color-border;
    color:white;
  }

  &-mainColor{
    @include button;
    background-color:#c8ea3c;
    border-radius: 5px;
    color:white;
    &:hover{
      background-color:#bbdb38;
      position: relative;  top:1%;
      font-weight: bold;
    }
  }
  &-gray{
    @include button;
    background-color:lightgray;
    border-color: gray;
    border-radius: 5px;
    color:$color-seconds;
  }
  &-danger{
    @include button;
    background-color:rgb(255, 60, 0);
    color:white;
    border-color: red;
    
  }

  &-confirm{
    @include button;
    background-color:$color-fourth;
    border-color: darken($color-fourth, 10);
    color:white;
    &:hover{
      background-color:darken($color-fourth, 5);
    }
  }

  &-selected{
    @include button;
    background-color: #1890ff;
    color:white;
  }
  &-update{
    @include button;
    background-color: #3ca0fd;
    color: white;
    &:hover{
      background-color: #1890ff;
      color: white;
    }
  }
  &-empty{
    @include button;
    border:1px solid #001529;
    background-color: white;
    color: black;
    &:hover{
      background-color: #1890ff;
      border: none;
      color: white;
    }
  }
  &.disabled{
    @include button;
    cursor: not-allowed;
    background-color: lightgray;
    &:hover{
      background-color: lightgray;
    }
  }
}

