.CheckBox{
    width: auto;
    height: 1.5rem;
    display: flex;
    align-items: center;
    gap : 0.3rem;
    cursor: pointer;

    input[type=checkbox]{
        display: none;
    }
    img{
        height: 60%;
        width: auto;
    }
    label{
        font-size: 100%;
        cursor: pointer;
    }
}