.character_status{
    width: 100%;
    height: 6%;
    display: flex;
    background-color: $color-third;
    animation: fade-in 2s;

    @mixin title {
        width:30%;
        height:80%;
        background:white;
        display:flex;
        justify-content:center;
        border: 2px outset $color-primary;
        border-right: none;
        align-items:center;
        border-top-left-radius:10px;
        border-bottom-left-radius:10px;
    }
    @mixin contents {
        width:50%;
        height:80%;
        background:white;
        border:2px outset $color-primary;
        display:flex;
        justify-content:start;
        align-items:center;
        padding-left:1vw;
        border-top-right-radius:10px;
        border-bottom-right-radius:10px;
        .status{
            width: 85%;
            height: 100%;
            display:flex;
            justify-content:center;
            align-items:center;
        }
        .edit{
            width: 15%;
            height: 100%;
            display:flex;
            justify-content:center;
            align-items:center;
            border-left: 2px solid $color-primary;
        }
    }


    .character_name{
        width: 50%;
        display: flex;
        justify-content:center;
        align-items:center;
        .title{
            @include title();
        }
        .contents{
            @include contents();
        }
    }

    .character_money{
        width: 50%;
        display: flex;
        justify-content:center;
        align-items:center;
        .title{
            @include title();
            width: 15%
        }
        .contents{
            @include contents();
            width:70%;
        }
    }
}