
.shootingMessage{
    display:inline-block;
    background-color: white;
    border : 3px solid dodgerblue;
    border-radius: 10px;
    padding: 0.5rem;
    position: absolute;
    color: dodgerblue;
    right: -45%;
    bottom: 35%;
    z-index: 10;
    .star{
        color: gold;
    }
    &.appear {
        animation: ytb_in_mobile 1s;
        animation-fill-mode: forwards;
        display: inline-block;
    }
      
    &.disappear {
        animation: ytb_out_mobile 1s;
        animation-fill-mode: forwards;
    }

    .youtube_alert{
        display: grid;
        grid-template-columns: 1fr 2fr;
        grid-template-rows: 1fr 1fr;
        img{
            grid-row: span 2;
        }
        div{
            display: flex;
            justify-content: center;
            align-items: center;
        }
    }
}


.shootingDonation{
    background-color: rgba(255, 255, 255, 0.65);
    border : 3px solid dodgerblue;
    border-radius: 10px;
    padding: 0.5rem;
    position: absolute;
    color: dodgerblue;
    right: -45%;
    top: 15%;
    text-align: center;
    
    &.appear {
        animation: donation_in_mobile 1s;
        animation-fill-mode: forwards;
    }
      
    &.disappear {
        animation: donation_out_mobile 1s;
        animation-fill-mode: forwards;
    }
    .donationImage{
        width: 8vw;
    }
    .donationText{
        line-height: 2.5vh;
        .star{
            color: gold;
            font-size: 6vw;
        }
    }
}


.shootingComment{
    display:inline-block;
    background-color: white;
    border : 3px solid dodgerblue;
    border-radius: 10px;
    padding: 0.5rem;
    position: absolute;
    color: dodgerblue;
    left: 30%;
    top: -5vw;
    max-width: 83%;
    font-size: 3.5vw;

    &::after{
        content: '';
        position: absolute;
        border-style: solid;
        border-width: 11px 14px 0;
        border-color: #FFFFFF transparent;
        display: block;
        width: 0;
        z-index: 1;
        bottom: -11px;
        left: 50%;
    }
    &::before{
        content: '';
        position: absolute;
        border-style: solid;
        border-width: 13px 16px 0;
        border-color: dodgerblue transparent;
        display: block;
        width: 0;
        z-index: 0;
        bottom: -16px;
        left: 50%;
    }

    &.appear {
        display: flex;
    }
      
    &.disappear {
        display: none;
    }

}

.toast_box{
    width: 100%;
    height: 5vh;
    top: 6vh;
    display: grid;
    gap: 10%;
    justify-content: center;
    position: fixed;
    

    &.appear {
        animation: infomation_in_mobile 1s;
        animation-fill-mode: forwards;
    }
      
    &.disappear {
        animation: infomation_out_mobile 1s;
        animation-fill-mode: forwards;
    }
    .shooting_infomation{
        background-color: rgba(255, 255, 255,0.9);
        border : 3px solid dodgerblue;
        border-radius: 10px;
        padding: 0.5rem;
        color: dodgerblue;
        // left: 15%;
        text-align: center;
        font-size: medium;

        &.red{
            border : 3px solid tomato;
            color: tomato;
        }
        
        &.appear {
            animation: infomation_in 1s;
            animation-fill-mode: forwards;
        }
          
        &.disappear {
            animation: infomation_out 1s;
            animation-fill-mode: forwards;
        }

    }

}
