@import url("https://cdn.jsdelivr.net/npm/bootstrap-icons@1.5.0/font/bootstrap-icons.css");

$color-primary : #1e79f3;
$color-seconds : #00AFFF;
$color-third : #e2a64e;
$color-fourth : #9E7700;
$color-fifth : #FFEECA;
$color-sixth : #C2A875;
$color-border : #0048B7;


@font-face {
    font-family: "FontBlack";
    src: url(./scss/font/CookieRun_Black.otf);
}
@font-face {
    font-family: "FontB";
    src: url(./scss/font/CookieRun_Bold.otf);
}
@font-face {
    font-family: "FontR";
    src: url(./scss/font/DungGeunMo.woff);
}

* {

    box-sizing: border-box;
    font-family: FontR !important;
    // 웹뷰 하이라이트 삭제
    -webkit-tap-highlight-color : transparent;

    //블럭 설정 막음
    // -ms-user-select: none; 
    // -moz-user-select: -moz-none;
    // -khtml-user-select: none;
    // -webkit-user-select: none;
    // user-select: auto;
}

input, textarea {
    -moz-user-select: auto;
    -webkit-user-select: auto;
    -ms-user-select: auto;
    user-select: auto;
}

@import './scss/index';