.character_choice{
    width: 100%;
    height: 100%;
    background-color: lightgray;
    position: relative;
    .box{
        top: 20%;
        position: absolute;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        gap: 2vh;
        width: 100%;
        animation: fade-in 2s;

        

        .game_title{
            display: block;
            color: goldenrod;
            font-size: 10vw;
            font-weight: 700;
            text-shadow: -2px 0 $color-primary, 0 2px $color-primary, 2px 0 $color-primary, 0 -2px $color-primary;
            margin-left: 2px;
            margin-right: 2px;
        }
        .game_sub_title{
            display: block;
            color: goldenrod;
            font-size: 7vw;
            text-shadow: -1px 0 $color-primary, 0 1px $color-primary, 1px 0 $color-primary, 0 -1px $color-primary;
            margin-left: 2px;
            margin-right: 2px;
            margin-bottom: 3vh;
        }

        [class^=button]{
            padding: 2vh;
        }
    }
}

    
.Landing{
    width: 100%;
    height: 100%;
    background-color: black;
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 6vw;
    .loading_text{

    }
    .opening_img{
        width: 100%;
        height: auto;
    }
}

.intro{
    width: 100%;
    height: 100%;
    background-color: black;
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 6vw;
    position: relative;
    animation: fade-in 2s;

    .text_container{
        width: 80%;
        height: 80%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        word-break: keep-all;
        line-height: 8vw;
        animation: fade-in 0.5;

        .crying{
            width: 50vw;
            height: 50vw;
        }
        .crying1{
            width: 50vw;
            height: 50vw;
        }
        .crying2{
            width: 50vw;
            height: 50vw;
        }
        .crying3{
            width: 50vw;
            height: 50vw;
        }
        .honjul{
            width: 50vw;
            height: 50vw;
        }

        .small{
            font-size: 4vw;
        }
        .big{
            font-size: 8vw;
        }
        .sigong{
            color: $color-primary;
            text-shadow: -1px 0 $color-third, 0 1px $color-third, 1px 0 $color-third, 0 -1px $color-third;
        }
    }

    .btn_wrapper{
        position: absolute;
        bottom: 10vw;
        width: 100%;
        height: 5%;
        display: flex;
        justify-content: space-around;
        align-items: center;
        .jump_btn{
            font-size: 5vw;
            border-bottom: 3px solid white;
            padding-bottom: 1vw;
        }
        .check_btn{
            font-size: 5vw;
            border-bottom: 3px solid white;
            padding-bottom: 1vw;
    
        }
    }
}
.character_status_wrapper{
    width: 100%;
    height: 100%;
    background-color: black;
    display: flex;
    align-items: center;
    justify-content: center;
    .body{
        width: 80%;
        max-width: 80%;
        border: 3px outset $color-primary;
        background-color: white;
        border-radius: 20px;
        padding: 5vh 10vw;

        display: flex;
        flex-direction: column;
        gap: 2vmax;
        .row{
            display: flex;
            justify-content: center;
            width: 100%;
            height: 4vh;

            div{
                display: flex;
                justify-content: center;
                align-items: center;
            }
            .title{
                width: 30%;
            }
            .contents{
                width: 70%;
                text-align: center;
                select{
                    width: 100%;
                    height: 100%;
                    text-align: center;
                }
            }
            .dice_btn{
                width: 100%;
                height: 100%;
                display: flex;
                gap: 1vmin;
            }
        }
    }
    
}
.ending_wrapper{
    width: 100%;
    height: 100%;
    background-color: black;
    padding: 2vh 10vw;
    .credit_title{
        width: 100%;
        display: flex;
        justify-content: center;
        color : white;
        font-size: large;
        margin-bottom: 1vh;
        transition: 0.5s;
    }
    .credit{
        width: 100%;
        height: 85vh;
        border: 5px outset $color-primary;
        border-radius: 20px;
        background-color: white;
        gap: 2vh;
        overflow-y: auto;
        padding: 1vw;
        transition: 1s;
        animation-delay: 0.5s;
        div{
            margin-top: 1vh;
        }
        .title{
            margin-top: 1vh;
            font-size: large;
            font-weight: 700;
            width: 100%;
            display: flex;
            justify-content: center;
            align-items: center;
        }
        .sub_title{
            margin-top: 3vh;
            width: 100%;
            font-size: larger;
            &::before{
                content: "- ";
            }
        }
        .contents_wrapper{
            border: 2px outset $color-seconds;
            padding: 1vh 1vw;
            border-radius: 10px;
            gap: 1vh;
            
            &.tags{
                height: 50vh;
                overflow-y: auto;
                div{
                    margin-top: 1vh;
                }
            }
        }
    }
    .confirm_btn{
        width: 100%;
        height: 10%;
        display: flex;
        align-items: center;
        justify-content: center;
        color: white;
    }
}