// 스트리머 정보
@mixin foldContents {
    height: 10vh;
    margin-top: 5%;
    padding-left: 5vw;
    padding-right: 5vw;
    border : 2px outset $color-primary;
    border-radius: 10px;
    position: relative;
    // &:active{
    //     border-style: inset;
    // }

    .title_wrapper{
        width: 100%;
        height: 8vh;
        display:flex;
        justify-content:space-between;
        align-items:center;
        .title{
            color : $color-primary;
            font-size:6vw;
            font-weight: 700;
            &.small{
                font-size: 4vw;
            }
            &.normal{
                font-size: 6vw;
            }
            &.large{
                font-size: 7vw;
            }
        }
        .value{
            max-width: 30%;
            height: 100%;
            display: flex;
            justify-content: center;
            align-items: center;
            color: $color-fourth;
            [class^=button]{
                font-weight: 300;
                height: 80%;
                padding: 0 1vw;
            }
        }
        
    }
    &.fold{
        display: flex;
        align-items: center;
        transition: 0.2s;
        .contents{
            display: none;
        }
    }
    &.unfold{
        height: 60%;
        overflow-y: auto;
        transition: 0.5s;
        padding-top: 2vw;
        border-color: $color-border;
        .title{
            color : $color-border;
        }
        .contents{
            display: grid;
            // grid-auto-rows: 20%;
            // gap: 3%;
            overflow-y: auto;
            // div{
            //     border: 4px outset $color-primary;
            //     border-radius: 20px;
            //     display:flex;
            //     justify-content:space-between;
            //     align-items:center;
            //     padding-left: 5vw;
            //     padding-right: 5vw;
            //     & > * {
            //         color : $color-primary !important;
            //         font-size:6vw;
            //         font-weight: 700;
            //     }
            // }
        }
    }
}

.panel{
    @include foldContents;
}

//캐릭터 모달
.char_modal_body{
    height:100%;
    overflow-y:auto;
}
//목록이 비어있는 경우
.emptyList{
    
}


//뉴스
.news_modal_body{
    width: 100%;
    height: 100%;

    .news_modal_contents{
        padding: 1vw;
        height: 20%;
        border : 4px outset $color-primary;
        border-radius: 20px;
        display: grid;
        grid-template-columns: 1fr 2fr;
        align-items: center;
        gap: 3%;
        margin-top: 5%;
        
        .thumbnail{
            width: 100%;
            height: 100%;
        }
        .title{
            color: black;
            font-weight: 700;
        }
    }
}
.new_detail_wrapper{
    .ModalContents{
        height: 60vh;
        .ModalContentsBody{
            //뉴스 상세
            .news_detail_container{
                height: 100%;

                .header{
                    display: grid;
                    grid-template-columns:1fr 1fr;
                    height: 25%;
                    gap:5%;

                    .thumbnail{
                        width: 100%;
                        height: 100%;
                    }
                    .title{
                        color: black;
                        display: flex;
                        align-items: center;
                        font-weight: 700;
                    }
                }
                .news_time{
                    color: black;
                    font-size: 2vh;
                    margin-top: 5%;
                }
                .contents{
                    white-space: pre-line;
                    color: black;
                    font-size: 2vh;
                    line-height: 3vh;
                }
                .effect{
                    .body{
                        color: black;
                        display: inline-block;
                        font-size: 1vmax;
                        span{
                            color: red;
                            font-size: 2vmax;
                            margin : {
                                left: 1vmax;
                                right : 1vmax;
                            };
                        }
                        .change{
                            &.plus{
                                color: red;
                            }
                            &.minus{
                                color: blue;
                            }
                        }
                    }
                }
                .duration{
                    color: black;
                    font-size: 1.5vmax;
                    display: flex;
                    justify-content: center;
                    span{
                        color: red;
                        font-size: 2vmax;
                        margin : {
                            left: 1vmax;
                            right : 1vmax;
                        };
                    }
                }
            }
        }
    }
}

.action_modal_wrapper{
    .ModalContents{
        height: 70vh;
        .ModalContentsBody{
            // 활동 선택 바디
            .action_modal_body{
                width: 100%;
                height: 100%;
                
            }
        }
    }
}

@mixin subTag {
    font-size: 1.5vh;
    display: flex;
    background-color: lightgray;
    text-align: center;
    border-radius: 5px;
    border: 2px outset;
    color: white;
    width: 100%;
    padding: 5%;
    justify-content: center;    
    align-items: center;
    word-break: keep-all;
    text-transform: uppercase;

    &.pro{
        color: black;
        background-color: gold;
        border-color: goldenrod;
    }
}

.video_manager_wrapper{
    .ModalContents{
        height: 70vh;
        .ModalContentsBody{
            //편집관리 모달 바디
            .video_manager_body{
                height: 100%;
                .search_bar{
                    width: 100%;
                    height: 5%;
                    display: flex;
                    gap: 1em;
                    align-items: center;
                }
                .video_list{
                    width: 100%;
                    height: 90%;
                    overflow: auto;
                    .video_contents{
                        border: 3px outset $color-primary;
                        border-radius: 10px;
                        padding: 1vw;
                        display: grid;
                        gap: 1vh;
                        margin-bottom: 2vw;
                        .control_grid{
                            display: flex;
                            width: 100%;
                            justify-content: flex-end;
                        }
                        .title{
                            width: 100%;
                            color: inherit;
                            white-space: nowrap;
                            text-overflow: ellipsis;
                            overflow: hidden;
                            &.untitle{
                                color:red;
                            }
                        }
                        .main_tag{
                            &.pro{
                                color: $color-seconds;
                            }
                        }
                        .sub_tag{
                            .tag_container{
                                display: grid;
                                grid-template-columns: repeat(3,1fr);
                                justify-items: center;
                                gap: 2vw;
                                span{
                                    @include subTag;
                                }
                            }
                        }
                        .progress_container{
                            display: flex;
                            align-items: center;
                            .title{
                                width: 20%;
                            }
                            .progress_bar{
                                width: 60%;
                                display: flex;
                                gap: 1vh;
                                align-items: center;
                                
                            }
                            .progress_number{
                                width: 18%;
                                display: flex;
                                justify-content: center;
                                
                            }
                        }
                        .setting_button{
                            display: flex;
                            [class^=button]{
                                width: 100%;
                            }
                        }
                        .editor_list{
                            display: grid;
                            grid-template-columns: repeat(3,1fr);
                            gap: 2vw;
                            div{
                                @include subTag();
                                font-size: medium;
                                display: flex;
                                justify-content: center;
                                padding: 5%;
                                background-color: $color-third;
                                border: 2px solid $color-fourth;
                            }
                            .mine{
                                background-color: $color-border;
                                border: 2px solid $color-primary;
                            }
                        }
                    }
                }
                .video_delete_button{
                    width: 100%;
                    height: 5%;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    background-color: tomato;
                    border-bottom-left-radius: 10px;
                    border-bottom-right-radius: 10px;
                    border: 3px outset tomato;
                    color: white;
                    &:active{
                        border-style: inset;
                    }
                }
            }
        }
    }
}

.progress_back{
    width: 100%;
    border: 2px solid $color-primary;
    border-radius: 5px;
    .progress_rating{
        height: 100%;
        background: #00c800;
        border-top-right-radius: 5px;
        border-bottom-right-radius: 5px;
        transition: 0.5s;
    }
}

.video_delete_modal{
    .ModalContents{
        height: 11vh;
        .ModalContentsBody{
            display: flex;
            flex-direction: column;
            gap: 3vh;

            .star_list{
                display: flex;
                justify-content: space-around;
            }
        }
    }
}


// 편집자 관리
.editor_manager{
    .ModalContents{
        height: 70vh;

        .editor_manager_body{
            height: 100%;
            .search_bar{
                display: flex;
                align-items: center;
                gap: 2vw;
                height: 8%;
                .search_input{
                    width: 25vw;
                }
                
            }
            
            .editor_list{
                height: 90%;
                overflow-y: auto;
                hr{
                    width: 100%;
                }
                .editor_container{
                    border: 2px solid $color-primary;
                    border-radius: 10px;
                    padding: 2vw;
                    display: grid;
                    gap: 1vh;
                    margin-bottom: 2vw;
                    .name_grid{
                        display: grid;
                        grid-template-columns: 3fr 1fr;
                    }
                    .abil_grid{
                        display: grid;
                        grid-template-columns: 1fr 1fr;
        
                    }
                }
                .tag_container{
                    display: grid;
                    grid-template-columns: 1fr 1fr 1fr;
                    gap: 2vw;
                    .tag{
                        @include subTag();
                    }
                }
                .editor_working_list{
                    display: grid;
                    gap: 1vh;
                    width: 100%;
                    .editor_working{
                        width: 100%;
                        display: grid;
                        gap: 0.5vh;
                        background-color: lighten($color-seconds, 20) ;
                        padding : 0.5vh;
                        border-radius: 10px;
                        border: 2px solid $color-seconds;
                        font-size: 1.5vh;
                        .header{
                            display: grid;
                            grid-template-columns: 3fr 1fr;
                            .title{
                                font-weight: 600;
                                width: 90%;
                                white-space: nowrap;
                                text-overflow: ellipsis;
                                overflow: hidden;
                            }
                        }
                    }
                } 
                .editor_add_button{
                    width: 100%;
                    border: 2px solid $color-primary;
                    border-radius: 10px;
                    padding: 2vw;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    font-size: 7vw;
                }  
                
            }
            .recall_container{
                display: flex;
                height: 8%;
                justify-content: center;
                align-items: center;
                gap: 5vw;
                .recall{
                    background-color: $color-seconds;
                    width: 25%;
                    height: 100%;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    border-radius: 10px;
                    color: white;
                }
            }
        }
    }
}


