                        border: 2px outset lightgray;
                        border-radius: 10px;
                        position: relative;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        .number{
                            position: absolute;
                            top: 5%;
                            left: 5%;
                            font-size: 8vw;
                        }
                        .mark{
                            font-size: larger;
                            font-size: 8vw;
                        }
                        &.hide{
                            div{
                                visibility: hidden;
                            }
