                        display: flex;
                        justify-content: center;
                    }
                }
                .player{
                    width: 100%;
                    height: 45%;
                    border: 2px outset $color-primary;
                    border-radius: 15px;
                    .card_panel{
                        height: 70%;
                    }
                    .player_sum{
                        display: flex;
                        justify-content: center;
                    }
                    .play_btn{
                        width: 100%;
                        height: 20%;
                        display: flex;
                        justify-content: space-around;
                    }
                }
            }
            .btn_wrapper{
                display: flex;
                justify-content: space-around;
                align-items: center;
                height: 10%;
                .bet_money{
                    width: 50%;
                    height: 80%;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                }
                .start_btn{
                    width: 30%;
                    height: 80%;
                }
            }
            .close_wrapper{
                height: 5%;
                div{
                    height: 100%;
                }
            }
        }
    }
}
.blackjack_guide{
    .ModalContents{
        height: 50vh;
        .ModalContentsBody{
            display: flex;
            flex-direction: column;
            gap: 5%;
        }
    }
}


@mixin infoTitle {
    font-size: 6vw;
}
@mixin priceInput {
    margin-top: 2vw;
    margin-bottom: 2vw;
    align-items: center;
    input{
        width: 20vw;
        text-align: center;
    }
}
@mixin price {
    margin-top: 2vw;
    margin-bottom: 2vw;
}
@mixin modalButton {
    justify-content: space-around;
}
.coin_buy_modal{
    .ModalContents{
        height: 25vh;
        .coin_buy{
            width: 100%;
            height: 100%;
            .title{
                @include infoTitle();
                height: 10%;
            }
            .check{
                height: 10%;
                text-decoration: underline;
            }
            .price{
                @include price();
                height: 15%;
            }
            .input{
                @include priceInput();
                height: 15%;
            }
            .button{
               @include modalButton();
               height: 25%;
            }
        }
        
    }
}

.depositPanel{
    
    .ModalContents{
        height: 150px;
        .deposit_modal_body{
            height: 90%;
            .deposit_title{
                margin-top : 2vw;
                margin-bottom: 2vw;
            }
            .deposit_price{
                @include price();
            }
            .deposit_input{
                @include priceInput();
                input{
                    width: 35vw !important;
                }
            }
            .deposit_button{
                @include modalButton();
            }
        }
    }

}

.loan_panel{
    
    .ModalContents{
        height: 150px !important;
        .loan_modal_body{
            height: 90%;
            .loan_title{
                margin-top: 2vw;
                margin-bottom: 2vw;
                span{
                    font-size: 3vw;
                }
            }
            .loan_price{
                @include price();
            }
            .loan_input{
