  


.tutotial_wrapper{
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.85);
    top: 0;
    left: 0;
    z-index: 0;

    .tutorial_text{
        color: white;
        width: 100%;
        padding: 1vh 1vw;
        position: relative;
        div{
            line-height: 125%;
            font-size: 120%;
            overflow-y: auto;
        }
        &.bottom{
            top: 65%;
        }
        &.top{
            top: 10%;
        }
        &.middle{
            top: 35%
        }
        .blue{
            text-shadow: -0.5px 0 $color-seconds, 0 0.5px $color-seconds, 0.5px 0 $color-seconds, 0 -0.5px $color-seconds;
            color: $color-primary;
        }
        .red{
            color: tomato;
        }

    }
    .control_btn{
        width: 100%;
        position: absolute;
        top: 0;
        height: 5%;
        color : white;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 0 5vw;
        font-size: larger;
        

        div{
            padding: 1vh 1vw;
            border-bottom: 2px solid white;
            border-top-left-radius: 10px;
            border-top-right-radius: 10px;

            &.next{
                animation: blinker 1s linear infinite;
            }
        }

    }
}