.char_monitor{
    width: 100%;
    height: 75%;
    background-color: $color-fifth;
    position: relative;
    animation: fade-in 2s;
    overflow-x: auto;

    .bg{
        height: 100%;
        width: 100%;
        .char_bg{
            .weather{
                position: absolute;
                width: auto;
                height: 100%;
            }
            .houseBg{
                position: absolute;
                height: 100%;
                width: auto;
            }
            .house_1{
                &.dust{
                    position: absolute;
                    height: 5%;
                    width: auto;
                    &.a{
                        bottom: 5%;
                        right: 30%;
                    }
                    &.b{
                        bottom: 2%;
                        right: 10%;
                    }
                    &.c{
                        bottom: 3%;
                        right: 80%;
                    }
                    &.d{
                        bottom: 8%;
                        right: 50%;
                    }
                }
                &.bag{
                    position: absolute;
                    bottom: 8%;
                    right: 0%;
                    height: 16%;
                    width: auto;
                }
            }
            .house_2{
                &.plant{
                    position: absolute;
                    height: 30%;
                    width: auto;
                    top: 8%;
                    right: -45%;
                }
                &.carrier{
                    position: absolute;
                    height: 30%;
                    width: auto;
                    bottom: 5%;
                    right: 0%;
                }
                &.shelf{
                    position: absolute;
                    height: 18%;
                    width: auto;
                    bottom: 5%;
                    left: 65%;
                }
                &.light{
                    position: absolute;
                    height: 15%;
                    width: auto;
                    bottom: 20%;
                    left: 67%;
                }
                &.badprop{
                    position: absolute;
                    height: 15%;
                    width: auto;
                    bottom: 1%;
                    left: 25%;
                }
                &.dreamcatcher{
                    position: absolute;
                    height: 25%;
                    width: auto;
                    top: 35%;
                    left: 20%;
                }
            }
            .house_3{
                &.shoper{
                    position: absolute;
                    bottom: 30px;
                    right: -100%;
                    height: 30%;
                }
                &.light{
                    position: absolute;
                    bottom: 30px;
                    left: 65%;
                    height: 35%;
                }
                &.plant{
                    position: absolute;
                    top: 28%;
                    left: 78%;
                    height: 15%;
                }
                &.drawing{
                    position: absolute;
                    top: 28%;
                    left: 60%;
                    height: 15%;
                }
                &.aircon{
                    position: absolute;
                    top: 16%;
                    left: 20%;
                    width: 35%;
                }
            }
            .house_4{
                &.aircon{
                    position: absolute;
                    height: 60%;
                    bottom: 8%;
                    right: -110%;
                }
                &.light{
                    position: absolute;
                    height: 20%;
                    top: 0;
                    right: -30%;
                }
                &.clock{
                    position: absolute;
                    height: 30%;
                    top: 10%;
                    left: 75%;
                }
                &.shelf{
                    position: absolute;
                    height: 40%;
                    bottom: 15%;
                    left: 70%;
                }
                &.plant{
                    position: absolute;
                    height: 20%;
                    top: 25%;
                    left: 25%;
                }
            }
            .house_5{
                &.aircon{
                    position: absolute;
                    height: 60%;
                    bottom: 8%;
                    left: 0%;
                    transform: rotateY(180deg);
                }
                &.drawing{
                    position: absolute;
                    height: 30%;
                    top: 25%;
                    left: 80%;
                }
                &.tropy{
                    position: absolute;
                    height: 50%;
                    bottom: 15%;
                    left: 45%;
                }
                &.carpet{
                    position: absolute;
                    width: 65%;
                    bottom: 0%;
                    left: 85%;
                }
                &.table{
                    position: absolute;
                    height: 15%;
                    bottom: 1%;
                    left: 60%;
                    z-index: 1;
                }
                &.plant{
                    position: absolute;
                    height: 30%;
                    bottom: 10%;
                    right: -100%;
                }
                &.light{
                    position: absolute;
                    height: 25%;
                    top: 0;
                    right: -35%;
                }
            }
        }
        .character_container{
            position: absolute;
            width: 50%;
            height: 55%;
            bottom: 0;
            left: 25%;
            img{
                position: absolute;
                width: 80%;
                bottom: 0;
                &.camera {
                    right: 0;
                }
            }
        }
        .resting{
            position: absolute;
            width: 70%;
            bottom: 0%;
            img{
                width:35%;
                height:auto;
            }
        }
        .exercise{
            position: absolute;
            width: 50%;
            height: 50%;
            bottom: 0%;
            left: 30%;
        }
    }
    //방송중 버튼
    .onAir{
        display: inline-block;
        padding: 1vmin;
        border: 2px solid;
        font-size: 4vmin;
        position: absolute;
        top: 5%;
        left: 4vw;
        background-color: gray;
        
        &.activate{
            background-color: tomato;
            color: white;
        }
    }
    // 방송시간 업타임
    .upTime{
        display: inline-block;
        padding: 1vmin;
        background-color: $color-third;
        border: 2px solid $color-primary;
        color : white;
        font-size: 4vmin;
        position: absolute;
        top: 5%;
        left: 23vw;
    }
    // 시청자수
    .viewer{
        display: flex;
        width: auto;
        padding: 1vmin;
        background-color: white;
        border: 2px solid $color-primary;
        color : $color-primary;
        font-size: 4vmin;
        position: absolute;
        top: 5%;
        left: 13vw;
        align-items: center;
        
        svg{
            width: 3vmin;
            height: 3vmin;
        }
        .count{
            margin-left: 2vw;
            display: flex;
            justify-content: end;
            align-items: center;
        }
    }

    .elapsed_date{
        position: absolute;
        top: 5%;
        right: 4vw;
        border: 2px solid $color-primary;
        background-color: $color-primary;
        color: white;
        padding: 1vmin;
        font-size: x-large;
    }

    //캐릭터 태그 버튼
    .tag_info{
        width: 8vmin;
        height: 8vmin;
        border: 2px outset $color-primary;
        background-color: white;
        border-radius: 50%;
        position: absolute;
        top : 65%;
        right: 2vh;
        
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 4vmin;
        color : $color-primary;
    }


    //캐릭터 상태창 버튼
    .char_info{
        width: 8vmin;
        height: 8vmin;
        border: 2px outset $color-primary;
        background-color: white;
        border-radius: 50%;
        position: absolute;
        top : 80%;
        right: 2vh;
        
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 4vmin;
        color : $color-primary;
    }

    .guide_info{
        width: 8vmin;
        height: 8vmin;
        border: 2px outset $color-primary;
        background-color: white;
        border-radius: 50%;
        position: absolute;
        top : 50%;
        right: 2vh;
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 4vmin;
        color : $color-primary;
    }
    .use_item_info{
        top : 18%;
        left: 8vh;
        &.fold{
            width: 8vmin;
            height: 8vmin;
            border: 2px outset $color-primary;
            background-color: white;
            border-radius: 50%;
            position: absolute;
            
            display: flex;
            justify-content: center;
            align-items: center;
            font-size: 4vmin;
            color : $color-primary;
        }
        &.unfold{
            display: flex;
            flex-direction: column;
            align-items: flex-end;
            position: fixed;
            width: auto;
            max-height: 130px;
            overflow-y: auto;
            background-color: rgba(0, 0, 0, 0.6);
            padding: 6px;
            border-radius: 10px;
            .title{
                color: white;
            }
            .use{
                color: white;
            }
            .sideEffect{
                color: white;
                .time{
                    color : red;
                    animation: textBlink 1s linear infinite;
                    background-color: white;
                }
            }
        }
        
    }
}

.control_box{
    position: unset;
    border: none;    
    width: 100%;
    height: 100%;
    display: grid;
    grid-template-columns: repeat(3,1fr);
    grid-auto-rows: 150px;
    justify-items: center;
    align-items: center;
    overflow-y: auto;
    background-color: $color-primary;
    animation: fade-in 2s;
    gap: 0;
    padding: 0;
    
    .control_grid{

        width:80%;
        height:80%;
        display:flex;
        justify-content:center;
        align-items:center;

        .control_btn{
            width:100%;
            height:100%;
            display:grid;
            grid-template-rows:4fr 2fr;
            border-radius: 10px;
            background-color: white;
            border: 3px outset $color-border;
            border-left: none;
            border-top: none;
            box-sizing: border-box;
            color : $color-primary;
            position: relative;
            &:active{
                border: 3px inset $color-border;
                border-right: none;
                border-bottom: none;
            }
            .badge{
                position: absolute;
                width: 22%;
                height: 22%;
                background-color: red;
                border-radius: 100%;
                right: -6%;
                top: -6%;
                font-size: 3vw;
                color: white;
                display: none;
                justify-content: center;
                align-items: center;
                transition: 1s;
                &.show {
                    animation: fade-in 1s;
                    animation-fill-mode: forwards;
                    display: flex;
                }

                &.progress{
                    background-color: $color-third;
                    left : -6%;
                }
            }
    
            .control_icon{
                display: flex;
                justify-content: center;
                align-items: center;
                font-size: 5vmin;
                svg{
                    width: 40%;
                }
            }
            .btn_text{
                display: flex;
                justify-content: center;
                align-items: center;
                font-size: 2vmin;
                word-break: keep-all;
                text-align: center;
            }
        }
    }

    
}