
    @keyframes ytb_in_mobile {
        from {
            opacity: 0;
            bottom: 36%;
        }
        to {
            opacity: 1;
        }
    }
        
    @keyframes ytb_out_mobile {
        from {
            opacity: 1;
        }
        to {
            opacity: 0;
            bottom:42%;
        }
    }
    @keyframes donation_in_mobile {
        from {
            opacity: 0;
            top: 12%;
        }
        to {
            opacity: 1;
        }
    }
        
    @keyframes donation_out_mobile {
        from {
            opacity: 1;
        }
        to {
            opacity: 0;
        }
    }

    
    @keyframes infomation_in_mobile {
        from {
            opacity: 0;
            top: 12%;
        }
        to {
            opacity: 1;
        }
    }
        
    @keyframes infomation_out_mobile {
        from {
            opacity: 1;
        }
        to {
            opacity: 0;
        }
    }

    @keyframes blinker {
        50% {
            color: black;
            background-color: white;
        }
    }
    @keyframes textBlink {
        0%{
            opacity: 0;
        }
        50%{
            opacity: 1;
        }
        100%{
            opacity: 0;
        }
    }