.stress_bar{
    height: 5vh;
    width: auto;
    display: grid;
    color: #FBEAFF;
    background-color: lighten($color-primary, 10);
    padding: 1%;
    grid-template-columns: 15% 65% 18%;

    animation: fade-in 2s;
    div{
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 3vmin;
    }
    .stress_name{
        font-size: 4vw;
    }

    .bar{
        background-color: lightgray;
        width: 100%;
        height: 5vmin;
        justify-content:flex-start;
        border-radius: 3px;
        .current{
            height:100%;
            border-radius: 3px;
            transition: 0.5s;
        }
    }
    .stress_number{
        font-size: 3vw;
    }
}